<template>
	<view>
		<view class="content">
			<view class="list">
				<view class="row" @click="chooseImage">
					<view class="title">{{details.head}}</view>
					<view class="right"><view class="tis">
					<image :src="user.face" mode="widthFix"></image>
					</view><view class="icon xiangyou"></view></view>
				</view>
				<view class="row" @click="toPage('../myname/myname')">
					<view class="title">{{details.nickname}}</view>
					<view class="right"><view class="tis">{{user.userName}}</view><view class="icon xiangyou"></view></view>
				</view>
				<view class="row" @click="toPage('../signature/signature')">
					<view class="title">{{details.signature}}</view>
					<view class="right"><view class="tis"></view><view class="icon xiangyou"></view></view>
				</view>
				<!-- <view class="row">
					<view class="title">收货地址</view>
					<view class="right"><view class="tis"></view><view class="icon xiangyou"></view></view>
				</view> -->
				<!-- <view class="row" @click="toPage('../security/security')">
					<view class="title">{{details.security}}</view>
					<view class="right"><view class="tis"></view><view class="icon xiangyou"></view></view>
				</view> -->
			</view>
			<view class="titleType">{{details.titleType}}</view>
			<view class="list">
					<view class="row" @click="toPage('./phone/phone')">
						<view class="title">{{details.phone}}</view>
						<view class="right"><view class="tis"></view><view class="icon xiangyou"></view></view>
					</view>
					<!-- <view class="row" @click="toPage('../security/security')">
						<view class="title">{{details.WechatID}}</view>
						<view class="right"><view class="tis"></view><view class="icon xiangyou"></view></view>
					</view>
					<view class="row" @click="toPage('../security/security')">
						<view class="title">{{details.qq}}</view>
						<view class="right"><view class="tis"></view><view class="icon xiangyou"></view></view>
					</view> -->
					<view class="row" @click="toPage('./mail/mail')">
						<view class="title">{{details.mail}}</view>
						<view class="right"><view class="tis"></view><view class="icon xiangyou"></view></view>
					</view>
			</view>
			</view>
			<choose-image ref="child" :imgList="imgList" :langth="1"></choose-image>
			</view>
			
	</view>
</template>

<script>
	import JSON from "../../../static/json/personal.json"
	import chooseImage from "../../../components/chooseImage.vue"
	export default {
		components:{
			chooseImage
		},
		data() {
			return {
				user:'',
				details:{},
				imgList:[]
			};
		},
		onLoad(){
			uni.getStorage({
				key:'lang',
				success:res=>{
					this.details=JSON[res.data]
					uni.setNavigationBarTitle({
						title:JSON[res.data].title
					})
				}
			})
			
	
		},
		onShow() {
			uni.getStorage({
				key:"userInfo",
				success:res=>{
					console.log(res)
					this.user=res.data;
				}
			})
		},
		methods:{
			chooseImage(){
				this.$refs.child.animationShow()
			},
			toPage(url){
				if(!url){
					uni.showToast({title: '模板未包含此页面',icon:"none"});return;
				}
				uni.navigateTo({
					url:url
				})
			},
		}
	}
</script>

<style lang="scss">
	page{
		background-color: #f3f3f3;	
	}
	
	.icon {
		font-size: 30upx;
	
	}
	.content{
		padding-bottom: 20upx;
		.titleType{
			width: 94%;
			margin: 0 auto 20upx auto;
			color: #999;
			font-size: 25upx;
		}
		.list{
			width: 96%;
			padding-left: 4%;
			background-color: #fff;
			margin-bottom: 20upx;
			.row{
				widows: 100%;
				min-height: 90upx;
				display: flex;
				align-items: center;
				justify-content: space-between;
				border-bottom: solid 1upx #eee;
				&:last-child{
					border-bottom: none;
				}
				.title{
					font-size: 32upx;
					color: #333;
				}
				.right{
					display: flex;
					align-items: center;
					color: #999;
					.tis{
						font-size: 26upx;
						margin-right: 5upx;
						max-height: 120upx;
						image{
							width: 100upx;
							height: 100upx;
							border-radius: 100%;
							margin: 10upx 0;
						}
					}
					.icon{
						width: 40upx;
						color: #cecece;
					}
				}
				
			}
		}
	}
</style>
